import React from 'react'
import "./Home.sass"
import { ContentLayout } from '../../components/Layout/ContentLayout';
import { graphql, Link } from 'gatsby';
import { ICustomerStory, IGatsbyLocation } from '../../interfaces';
import { HomeCustomerStory } from '../../components/CustomerStory/HomeCustomerStory';
import { FaChalkboardTeacher, FaChartBar, FaShippingFast, FaPhone } from 'react-icons/fa';
import { IHeaderProps } from '../../components/Layout/Header';

export class Home extends React.Component<{stories: ICustomerStory[], displayedStories?: number, location: IGatsbyLocation}>
{
  constructor(props)
  {
    super(props);
  }
  render()
  {
    let stories: ICustomerStory[] = this.props.stories;
    let header: IHeaderProps = {
      description: "Artikel, Tipps&Tricks, Online Kurse zu Excel Power Pivot, Power BI und Power Query | Trainings, Workshops und Implementierungsunterstützung durch Macher",
      title: "Home | Power BI Insights | Durchblick durch Daten",
      type: "website",
      url: "/"
    }

    let homepageStories = {
      kom4tec: stories.find(s => s.fields.story.company.toLowerCase().indexOf("kom4tec") >= 0),
      endress: stories.find(s => s.fields.story.company.toLowerCase().indexOf("endress") >= 0),
      flexsec: stories.find(s => s.fields.story.company.toLowerCase().indexOf("flex") >= 0),
      theobald: stories.find(s => s.fields.story.company.toLowerCase().indexOf("theobald") >= 0),
    }

    return (
      <ContentLayout header={header}>
        <div className="container-nosidebar">
          <div className="section">
            <h1>Power BI Insights – der BI-Blog für den Durchblick durch Daten</h1>
            <p>
              Hier teilen wir unsere Leidenschaft für Datenauswertungen.
            </p>
          </div>
          <div className="section">
            <HomeCustomerStory stories={[
              homepageStories.endress,
              homepageStories.kom4tec
            ]}>
            </HomeCustomerStory>
          </div>
          <div className="section">
            <h1>Neueinsteiger?</h1>
            <ul>
              <li>
                <Link to="/was-ist-power-bi">Was ist Power BI?</Link>
              </li>
              <li>
                Artikel zu Power Query auf <a href="https://powerpivotinsights.de/category/power-query">www.powerpivotinsights.de</a>
              </li>
              <li>
                Artikel zu Excel Power Pivot auf <a href="https://powerpivotinsights.de/category/power-pivot">www.powerpivotinsights.de</a>
              </li>
            </ul>
          </div>
          <div className="section">
            <HomeCustomerStory stories={[
              homepageStories.flexsec,
              homepageStories.theobald
            ]}>
            </HomeCustomerStory>
          </div>
          <div className="section">
            <h1>Unsere Angebote</h1>
            <div className="columns">
              <div className="column homepage-offer">
                <Link to="/trainings">
                  <div>
                    <FaChalkboardTeacher/>
                  </div>
                  <div>
                    Trainings
                  </div>
                </Link>
              </div>
              <div className="column">
              <div className="column homepage-offer">
                <Link to="/trainings">
                  <div>
                    <FaChartBar/>
                  </div>
                  <div>
                  Umsetzung
                  </div>
                </Link>
              </div>
              </div>
              <div className="column">
              <div className="column homepage-offer">
                <Link to="/trainings"> {/*TODO: link*/}
                  <div>
                    <FaShippingFast/>
                  </div>
                  <div>
                    Schnelle Unterstützung
                  </div>
                </Link>
              </div>
              </div>
            </div>
          </div>
          <div className="section">
            <div style={{textAlign: "center", fontSize: "32pt"}}>
              <a href="tel:+49(0)711/2172878-0">
                <span className="text-primary"><FaPhone/></span> 
                <span className="text-black" style={{fontWeight: "bold"}}> +49 (0) 711/2172878-0 </span>
              </a>
            </div>
          </div>

        </div>
      </ContentLayout>
    )
  }
}


const Page = ({data, pageContext, location}) => {
  return (
    <Home stories={data.stories.edges.map(e => e.node)} location={location}></Home>
  );
};
export default Page;

export const pageQuery = graphql`
  {
    ...customerStories
  }
`;
